body, html {
  background-color: #f5f5f520;
  height: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.container {
  padding: 20px;
}

h1, h2, h3 {
  margin-top: 0;
}

h3 {
  color: #333;
  border-bottom: 1px solid #e4e4e4;
  margin-top: 0;
  margin-bottom: 5px;
  padding-bottom: 2px;
  font-size: 14px;
}

.btn {
  transition: all .3s;
}

.btn:hover {
  transform: scale(1.05);
}

.btn-upload {
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: all .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.btn-upload:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px #0003;
}

#cy {
  background-color: #e9ecef;
  border: 1px solid #ccc;
  height: 100%;
}

#info, #walks {
  border: 1px solid #ccc;
  flex: auto;
  height: 100%;
  overflow-y: auto;
}

#right-container {
  flex-direction: column;
  height: 100%;
  display: flex;
}

#walks div:hover {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  padding: 4px;
}

#jsoneditor {
  width: 100%;
  height: 400px;
}

#tooltip {
  color: #fff;
  z-index: 9999;
  opacity: 0;
  background-color: #000c;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  transition: opacity .2s, transform .2s;
  display: none;
  position: absolute;
  transform: translateY(-10px);
  box-shadow: 0 4px 8px #0000001a;
}

#tooltip:before {
  content: "";
  border: 6px solid #0000;
  border-top-color: #000c;
  margin-left: -6px;
  position: absolute;
  top: 100%;
  left: 50%;
}

pre {
  cursor: pointer;
  max-height: 100px;
  transition: max-height .3s ease-in-out;
}

pre.expanded {
  max-height: 500px;
}

#infoContent .btn-link {
  color: #007bff;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

#infoContent .btn-link:hover {
  text-decoration: underline;
}

#infoContent pre {
  background-color: #f5f5f5;
  border-radius: 5px;
  max-height: 600px;
  padding: 20px;
  font-size: 1.1rem;
  overflow-y: auto;
}

.card, #info, #walks {
  border-radius: 10px;
  box-shadow: 0 4px 8px #0000001a;
}

header {
  color: #fff;
  background-color: #333;
}

a:hover, .clickable:hover {
  cursor: pointer;
  text-decoration: underline;
}

.element:hover {
  animation: 1s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.file-upload-container {
  justify-content: flex-start;
}

.igv-container {
  background-color: #f9f9f9;
  border: 1px solid #d6d6d6;
  border-radius: 5px;
  height: 100vh;
  box-shadow: 0 4px 6px #0000001a;
}

#selectedFileName {
  color: #555;
}

.navbar {
  height: 25px;
  font-size: .9rem;
}

.navbar-brand {
  letter-spacing: 1px;
  align-items: center;
  padding-left: 1rem;
  font-size: 1.2rem;
  display: flex;
}
